/*------------------------------------------------------------------
[1. Typography]
*/
body {
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  letter-spacing: 0;
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 1.8;
  color: #666;
}
h1,
h2,
h3,
h4,
h5,
h6,
input,
button,
.button,
select,
textarea,
.project-title,
.product-title,
.project-description,
.font-alt-1,
.header .navigation > ul > li > a,
.side-navigation > ul > li > a,
.overlay-navigation > ul > li > a,
.grid-filter-menu a {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
}
/* Links */
a,
p a,
.box a:not(.button) {
  color: #232323;
}
a.animated-link {
  position: relative;
}
a:hover,
p a:hover,
.box a:not(.button):hover,
.social-list li a:hover,
.post-info a:hover,
.widget a:hover,
.team-1 .social-list a:hover,
.team-2 .social-list a:hover,
.side-navigation-footer .social-list a:hover,
.accordion li a:hover,
.accordion li.active a,
.tabs li a:hover,
.tabs li.active a,
.tabs li.active a:hover,
.blog-masonry .with-background .post-read-more a:hover,
.overlay-navigation-wrapper .navigation-hide a,
.post-info-over a:hover,
.post-info-over a:hover span,
.post-author-aside a:hover,
.post-love a:hover,
.post-love a:hover span,
.navigation-hide a,
.footer .social-list a:hover,
.scroll-to-top a:hover,
.footer .list-group a:hover + .post-info .post-date,
#tml-exit,
.scroll-down a:hover,
.footer .navigation a:hover,
.footer .footer-top a:hover {
  color: #ff4556;
}
a[class*="icon-"]:hover {
  text-decoration: none;
}
a:hover .text-line,
a.animated-link:hover .text-line,
.accordion li a:hover .text-line,
.tabs li a:hover .text-line,
.blog-masonry .with-background .post-read-more a:hover .text-line {
  background-color: #ff4556;
}
/* Titles */
h1,
h2,
h3,
h4,
h5,
h6 {
  line-height: 1;
  font-weight: 700;
  text-transform: normal;
  color: #000;
}
h1 a:hover,
h2 a:hover,
h3 a:hover,
h4 a:hover,
h5 a:hover,
h6 a:hover {
  color: #ff4556;
}
.footer h1,
.footer h1 a,
.footer h2,
.footer h2 a,
.footer h3,
.footer h3 a,
.footer h4,
.footer h4 a,
.footer h5,
.footer h5 a,
.footer h6,
.footer h6 a {
  color: #999;
}
h1 {
  font-size: 4.5rem;
}
h2 {
  font-size: 3.3rem;
}
h3 {
  font-size: 2.3rem;
}
h4 {
  font-size: 1.8rem;
}
h5 {
  font-size: 1.6rem;
}
h6 {
  font-size: 1.2rem;
}
[class*="intro-title-"] h1,
.featured-media h1,
[class*="intro-title-"] h2,
.featured-media h2,
[class*="intro-title-"] h3,
.featured-media h3,
[class*="intro-title-"] h4,
.featured-media h4,
[class*="intro-title-"] h5,
.featured-media h5,
[class*="intro-title-"] h6,
.featured-media h6 {
  text-transform: uppercase;
}
/* Lead */
.lead,
blockquote.large {
  font-size: 2rem;
  font-weight: 300;
}
/* Sidebar Text */
.widget-title {
  font-size: 2rem;
}
.widget a {
  font-size: 1.3rem;
  color: #666;
}
/* Blog Titles & Text */
h2.post-title {
  margin-bottom: 3rem;
  font-weight: 700;
}
.blog-masonry h2.post-title {
  margin-bottom: 0;
  font-size: 3rem;
  text-transform: none;
  display: block;
}
.blog-masonry.masonry-set-dimensions h2 {
  font-size: 1.8rem;
}
.blog-masonry.masonry-set-dimensions h2 a,
.blog-masonry.masonry-set-dimensions .post-read-more a {
  color: #fff;
}
.blog-regular .post-title,
.blog-wide .post-title {
  font-size: 4rem;
}
.single-post-tags .tags-title {
  font-size: 1.3rem;
}
h3.widget-title {
  font-size: 1.6rem;
}
/* Team */
.team-1 h3,
.team-2 h3 {
  margin-top: 0.5rem;
}
.team-1 h6.occupation,
.team-2 h6.occupation {
  margin-bottom: 0.3rem;
}
/* Project Title */
h4.project-title {
  font-size: 1.4rem;
}
.overlay-info .project-description {
  font-size: 1.2rem;
  opacity: 0.7;
}
/* Rollover */
.overlay-info > span > span {
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
  color: #232323;
}
/* Pricing Table Title */
.pricing-table.medium .pricing-table-header h2 {
  font-size: 1.5rem;
}
.pricing-table.medium .pricing-table-price h4 {
  font-size: 3.8rem;
}
.pricing-table.medium .pricing-table-price .interval {
  font-size: 1.4rem;
}
/* Footer Text */
.footer h4 {
  font-size: 1.5rem;
  color: #000;
}
.footer .copyright {
  font-size: 1.1rem;
}
.footer p {
  font-size: 1.2rem;
}
/* Lead */
.lead {
  font-size: 2rem;
}
/* Title Classes */
.title-xlarge {
  font-size: 8rem;
}
.tms-caption.scale.title-xlarge,
.tmp-content .title-xlarge {
  font-size: 8rem;
  line-height: 7.5rem;
}
.title-large {
  font-size: 6.2rem;
}
.tms-caption.scale.title-large,
.tmp-content .title-large {
  font-size: 6.2rem;
  line-height: 5.7rem;
}
.title-medium {
  font-size: 3rem;
}
.tms-caption.scale.title-medium,
.tmp-content .title-medium {
  font-size: 3rem;
  line-height: 2.5rem;
}
.title-small {
  font-size: 1.1rem;
}
.tms-caption.scale.title-small,
.tmp-content .title-small {
  font-size: 1.1rem;
  line-height: 0.6rem;
}
/* Text Classes */
.text-xlarge {
  font-size: 4.6rem;
  line-height: 1.4;
}
.text-large {
  font-size: 4rem;
  line-height: 1.4;
}
.text-medium {
  font-size: 2.4rem;
  line-height: 1.4;
}
.text-small {
  font-size: 1.6rem;
  line-height: 1.4;
}
/* Weight Classes */
.weight-light {
  font-weight: 300;
}
.weight-bold {
  font-weight: 700;
}
@media only screen and (max-width: 768px) {
  h1,
  .title-xlarge {
    font-size: 4rem;
  }
  h2,
  .title-large {
    font-size: 2.8rem;
  }
  h3 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.5rem;
  }
  h5 {
    font-size: 1.3rem;
  }
  h6 {
    font-size: 1.1rem;
  }
  .blog-regular .post-title,
  .blog-wide .post-title {
    font-size: 3rem;
  }
  h5.tms-caption,
  .tmp-content h5 {
    font-size: 1.4rem;
  }
  .tms-caption.title-xlarge,
  .tmp-content .title-xlarge {
    font-size: 4.5rem;
    line-height: 4.5rem;
  }
  /* Lead */
  .lead {
    font-size: 1.7rem;
  }
  /* Text Classes */
  .text-xlarge {
    font-size: 3.1rem;
  }
  .text-large {
    font-size: 2.5rem;
  }
  .text-medium {
    font-size: 1.9rem;
  }
  .text-small {
    font-size: 1.1rem;
  }
  /* Helpers */
  .fullscreen-section .background-on-mobile {
    background: #f4f4f4;
  }
  .fullscreen-section .background-on-mobile * {
    color: #666 !important;
  }
  .fullscreen-section .background-on-mobile h1,
  .fullscreen-section .background-on-mobile h2,
  .fullscreen-section .background-on-mobile h3,
  .fullscreen-section .background-on-mobile h4,
  .fullscreen-section .background-on-mobile h5,
  .fullscreen-section .background-on-mobile h6 {
    color: #000 !important;
  }
}
