/*------------------------------------------------------------------
[3. Section Padding]
*/
/* Unique Template Padding & Margins */
.wrapper-inner {
  padding: 3rem;
}
.header .header-inner {
  padding-left: 3rem;
  padding-right: 3rem;
}
[class*="intro-title-"] + .section-block,
.featured-media + .section-block {
  padding-top: 8rem;
  border-top-width: 3rem;
  border-top-style: solid;
}
@media only screen and (min-width: 960px) {
  .header {
    border-top-width: 3rem;
    border-top-style: solid;
  }
  .header:not(.header-absolute):not(.header-fixed) {
    margin-top: -3rem;
    border-top-width: 0;
    border-top-style: solid;
  }
}
.section-block,
.fullscreen-section .fullscreen-inner,
.pagination-2 .pagination-next,
.row.xlarge {
  padding-top: 11rem;
  padding-bottom: 11rem;
}
.section-block.replicable-content,
.row.xlarge.replicable-content {
  padding-top: 11rem;
  padding-bottom: 8rem;
}
.section-block.slanted-top:before {
  width: 100%;
  height: 12rem;
  background-color: inherit;
  content: '';
  position: absolute;
  left: 0;
  top: -5.7rem;
  -webkit-transform: skewY(-4.5deg);
  -ms-transform: skewY(-4.5deg);
  transform: skewY(-4.5deg);
  z-index: 5;
}
.footer-1 .footer-top {
  padding-top: 9rem;
  padding-bottom: 5rem;
}
.grid-filter-menu {
  padding-top: 3rem !important;
  padding-bottom: 5rem;
}
.home-page .grid-filter-menu {
  padding-bottom: 4rem;
}
.section-block.feature-2 {
  padding-bottom: 12rem;
}
.divider {
  margin-top: 6rem;
  margin-bottom: 9rem;
}
.separator {
  width: 5rem;
  margin-top: 1.5rem;
  margin-bottom: 3rem;
}
.text-line {
  width: 2rem;
  height: 0.1rem;
  display: inline-block;
}
.text-line[class*="bkg-"] {
  padding: 0;
}
.text-line-vertical {
  display: inline-block;
  position: relative;
}
.text-line-vertical:after {
  width: 1px;
  height: 8rem;
  margin-top: 3rem;
  border-right: 1px solid #777;
  position: absolute;
  top: 100%;
  left: 50%;
  content: '';
}
@media only screen and (min-width: 960px) {
  .header-1-static + .content {
    padding-top: 10rem;
  }
}
@media only screen and (max-width: 768px) {
  .fullscreen-section.content-below-on-mobile .fullscreen-inner {
    padding-top: 12rem !important;
    padding-bottom: 12rem !important;
  }
}
/*------------------------------------------------------------------
[4. Header Styling]
*/
/* Initial Position */
.header-bottom {
  bottom: 0.1rem;
}
/* Intial Height */
.header .logo,
.header .header-inner .navigation > ul > li {
  height: 8rem;
  line-height: 8rem;
}
/* Compacted Height */
.header-compact .logo,
.header-compact .header-inner .navigation > ul > li {
  height: 6rem;
  line-height: 6rem;
}
/* Header Initial Styling */
.header .header-inner {
  background-color: #fff;
  border-bottom: none;
  /*border-bottom: 1px solid #eee;*/
}
.header .header-inner > .nav-bar {
  /*border-bottom: 1px solid #eee;*/
}
/* Unique Padding */
@media only screen and (min-width: 960px) {
  .header .header-inner {
    padding: 1rem 3rem 0 3rem;
  }
  .header .header-inner > .row {
    max-width: 100%;
    padding: 0 3rem;
  }
}
/* Transparent Header Initial & Active Nav Colors */
.header-transparent .header-inner {
  background-color: transparent;
  border-bottom: 1px solid transparent;
}
.header-transparent .header-inner > .nav-bar {
  border-bottom: 1px solid transparent;
}
/* Background Header - On Scroll! */
.header-background .header-inner {
  background-color: transparent;
  border-color: transparent;
}
.header-background .header-inner > .nav-bar {
  border-bottom: none;
}
/* Header Inner Top */
.header .header-inner-top {
  height: 4rem;
  background: #232323;
  color: #fff;
}
/* Mobile Header Styles & Breakpoints */
.mobile .header .header-inner {
  background-color: #fff;
  border-top: none;
}
.mobile .header .header-inner {
  border-bottom: 1px solid #eee;
}
.mobile .header + .content {
  padding-top: 0;
}
@media only screen and (max-width: 960px) {
  .header .header,
  .header .header-inner,
  .header.header-transparent .header-inner {
    background-color: #fff;
  }
  .header .header-inner {
    border-bottom: 1px solid #eee;
  }
  .header .header-inner > .nav-bar {
    padding: 0;
  }
}
/*------------------------------------------------------------------
[5. Header Logo]
*/
/* Initial Logo Width */
.header .logo {
  width: auto;
}
.header .logo a:first-child {
  width: 12rem;
}
.header .logo a:last-child {
  opacity: 0;
  width: 4.4rem;
  visibility: hidden;
}
/* Compacted Header Logo Width */
.header-compact .logo {
  width: 12rem;
}
/* Initial Logo Styling */
.header .logo a {
  padding: 0 1.4rem;
  line-height: 4.4rem;
  display: block !important;
  background-color: #000;
  float: left;
}
.header-in .logo a,
.header-out .logo a {
  -webkit-transition-duration: 0ms;
  transition-duration: 0ms;
}
.header .logo a:first-child {
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
/* Transparent Header Show Light Logo */
.header-transparent .logo a:first-child {
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
.header-transparent .logo a + a {
  display: block !important;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
/* Header Background Logo Swap to Dark Logo - On Scroll! */
.header-background .logo a:first-child {
  opacity: 0;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
.header.header-background .logo a:last-child {
  opacity: 1;
}
.header.header-background .logo a:first-child,
.header.header-background .logo a:last-child {
  -webkit-transform: translateX(-12rem);
  -ms-transform: translateX(-12rem);
  transform: translateX(-12rem);
  visibility: visible;
}
/* Logo Nav Dark */
.nav-dark .logo a:first-child {
  display: block !important;
  top: 0;
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
.nav-dark .logo a:last-child {
  display: none !important;
}
/* Logo Mobile Header & Breakpoints */
.mobile .header .logo a:first-child {
  display: block !important;
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}
@media only screen and (max-width: 960px) {
  .header .logo-inner {
    display: table-cell;
    vertical-align: middle;
  }
  .header .logo a {
    position: relative;
  }
  .header .logo a:first-child {
    opacity: 1;
    top: 0;
    -webkit-transform: translateX(0) !important;
    transform: translateX(0) !important;
  }
  .header .logo a:last-child {
    display: none !important;
  }
}
/*------------------------------------------------------------------
[6. Header Navigation]
*/
/* Padding & Margins */
.header .logo-center ~ .navigation.nav-center {
  padding-top: 0.5rem;
}
.header .logo-center ~ .navigation.nav-center > ul > li > a {
  padding-bottom: 2rem;
}
.header .navigation > ul > li > a {
  margin: 0 1.5rem;
  padding-left: 0;
  padding-right: 0;
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 0.1rem;
}
/* Header Initial & Active Nav Colors */
.header .navigation > ul > li > a {
  color: #999;
  opacity: 1;
}
.header .navigation > ul > li.current > a,
.header .navigation > ul > li > a:hover {
  color: #000;
  opacity: 1;
}
.header .navigation > ul > li.current > a:hover {
  color: #000;
}
/* Transparent Header Initial, Hover & Current Nav Colors */
.header-transparent .navigation > ul > li > a {
  color: #fff;
  opacity: 0.6;
}
.header-transparent .navigation > ul > li > a:hover {
  color: #fff;
  opacity: 1;
}
.header-transparent .navigation > ul > li.current > a,
